<template>
    <div class="yingyong">
        <headerBar :title="headerNavData" @gomycenter="gomycenter()"></headerBar>
        <yd-pullrefresh :callback="refresh" ref="pullrefreshDemo" loading-text="" pull-text="" drop-text="">
          <yd-infinitescroll :callback="loadList" ref="infinitescrollDemo" doneTip>
            <div class="tadelist" theme="1" slot="list">
              <div class="tade_item" v-for="item in list">
                <div class="trade_time">{{item.TradeDate}}</div>
                <div class="bbor" v-for="atm in item.Data">
                  <!-- @touchstart="showDeleteButton" @touchend="clearLoop" -->
                  <yd-flexbox class="trade_info" @click.native="goorder(atm)"  v-if="atm.tradeType == 1">
                      <div><span class="dian dianblue"></span></div>
                      <yd-flexbox-item class="trade_addr">
                        <span class="left_d" v-if="atm.tradeType == 1">{{atm.Sender | ordernum}}</span>
                        <p class="diancolorblue"><span>+ {{atm.Value}}</span> {{atm.Symbol}}</p>
                      </yd-flexbox-item>
                  </yd-flexbox>
                  <yd-flexbox class="trade_info" @click.native="goorder(atm)" v-else>
                      <div><span class="dian dianyellow"></span></div>
                      <yd-flexbox-item class="trade_addr">
                        <span class="left_d">{{atm.Receiver | ordernum}}</span>
                        <p class="diancoloryellow"><span>- {{atm.Value}}</span> {{atm.Symbol}}</p>
                      </yd-flexbox-item>
                  </yd-flexbox>
                  <yd-flexbox class="trade_dabao" @click.native="goorder(atm)">
                      <div><span class="zwf"></span></div>
                      <yd-flexbox-item class="succorfail">
                        <span v-if="atm.minutes > 10">{{atm.TradeTime | timefilter}}</span>
                        <span v-else>{{atm.minutes}}{{$t('data.Minutes')}}</span>
                        <i :class="{'green':atm.Status=='1','red':atm.Status=='0','grey':atm.Status=='-1'}">[{{atm.Status | orderStatusfilter}}]</i>
                      </yd-flexbox-item>
                  </yd-flexbox>
                  <!-- <yd-progressbar type="line" :progress="1" trail-width="1" trail-color="#278eed"></yd-progressbar> -->
                  <!-- <yd-progressbar type="line" :progress="1" trail-width="1" trail-color="#ed6d27"></yd-progressbar> -->
                </div>
              </div>
              <!-- - -->
            </div>
            <span slot="doneTip" v-show="list==[]">{{$t('toast.nodata')}}</span>
          </yd-infinitescroll>
        </yd-pullrefresh>
        <norecord :nodata="nodata" v-if="shownorecord"></norecord>
    </div>
  
</template>

<script>
  import headerBar from './header.vue'
  import norecord from './norecord.vue'
  import { getOrder } from '../assets/js/Keytoken.js'
  import {getCookie,setCookie} from '../assets/js/cookie.js'
export default {
  beforeCreate () {
    var url = this.$route.path
    setTimeout(function(){
      document.querySelector('body').setAttribute('style', 'background:#f4f4f4')
    },10)
  },
  beforeDestroy () {
    //document.querySelector('body').setAttribute('style', 'background:#ffffff')
  },
  components:{
      "headerBar":headerBar,
      "norecord":norecord
  },
  data () {
    return {
      headerNavData:{
         left:true,
         title:this.$t('mycenter.record'),
         right:false,
         bj:'#2c94ed',
         search:false,
         share:false,
         left_g:false
      },
      shownorecord:false,
      nodata:this.$t('toast.nolist'),
      page:1,
      pagesize:10,
      pagenum:0,
      list:[],
      lasttime:''
    }
    
  },
  created(){
    this.loadList()
    var type = this.$route.query.type
    if (type == 2) {
      this.headerNavData.left = false
      this.headerNavData.left_g = true
    }
  },
  methods:{
    //跳转交易记录详情
    goorder(item){
      var data = JSON.stringify(item)
      this.$router.push({path:'/index_order_detail',query:{data:data,type:1}})
    },
    clearLoop() {
        clearInterval(this.Loop);
    },
    //跳转个人中心
    gomycenter(){
        this.$router.push({path:'/mycenter',query:{state:5}})
    },
    refresh(){
      this.page=1
      this.pagesize=10
      this.list=[]
      this.loadList()
    },
    //交易列表查询
    loadList() {
      var that = this;
      this.openloading(this.$t('toast.loding'))
      this.axios({
        url:this.Url + 'EthTrade.ashx',
        method:'post',
        params: {req:4,publickey:getCookie("publickey"),pageindex:this.page,pagecount:this.pagesize}
      }).then(function (res) {
          that.closeloading()
          that.$refs.pullrefreshDemo.$emit('ydui.pullrefresh.finishLoad');
          var _list = res.data.data;
          if (res.data.success) {
            if (res.data.data.length == 0) {
              that.shownorecord = true
                that.$dialog.toast({
                    mes: that.$t('data.Nomoredata'),
                    timeout: 1500
                });
                return;
            }else{
              if (that.list.length > 0) {
                var lastdata = that.list[that.list.length - 1]
                var arry=[]
                for (var i = 0; i < _list.length; i++) {
                   arry=[]
                   if (lastdata.TradeDate == _list[i].TradeDate) {
                      lastdata.Data = lastdata.Data.concat(_list[i].Data)
                   }else{
                      arry.push(_list[i])
                      that.list = [...that.list, ...arry];
                   }
                   that.pagenum = res.data.totalCount
                   if (res.data.success=="false") {
                       /* 所有数据加载完毕 */
                       that.$refs.infinitescrollDemo.$emit('ydui.infinitescroll.loadedDone');
                       return;
                   }
                   that.$refs.infinitescrollDemo.$emit('ydui.infinitescroll.finishLoad');
                   that.page++;
                }
              }else{
                that.list = [...that.list, ..._list];
                that.pagenum = res.data.totalCount
                if (res.data.success=="false") {
                    /* 所有数据加载完毕 */
                    that.$refs.infinitescrollDemo.$emit('ydui.infinitescroll.loadedDone');
                    return;
                }
                that.$refs.infinitescrollDemo.$emit('ydui.infinitescroll.finishLoad');
                that.page++;
                that.$refs.infinitescrollDemo.$emit('ydui.infinitescroll.reInit')
              }
            }
          }else{
            if (res.data.data.length == 0 && that.list.length==0) {
              that.shownorecord = true
            }
            if (res.data.data.length == 0) {
                /* 所有数据加载完毕 */
                that.$refs.infinitescrollDemo.$emit('ydui.infinitescroll.loadedDone');
                return;
            }
            that.$refs.infinitescrollDemo.$emit('ydui.infinitescroll.finishLoad');
                that.$dialog.toast({
                    mes: that.$t('data.Nomoredata'),
                    timeout: 1500
                });
                return;
          }
      }).catch(function(res){
          that.closeloading()
          res = JSON.stringify(res)
          that.$refs.pullrefreshDemo.$emit('ydui.pullrefresh.finishLoad');
          if(JSON.stringify(JSON.parse(res).request)=="{}"){
              that.catchErr()
              setTimeout(function(){
                  that.$dialog.confirm({
                      title: that.$t('data.networkerr')+'...',
                      opts: (res) => {
                          that.loadList() 
                      }
                  });
              },2000)
          }
      });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.yingyong{
  padding-top: .9rem;
  font-family: -apple-system, BlinkMacSystemFont, "PingFang SC","Helvetica Neue",STHeiti,"Microsoft Yahei",Arial,Tahoma,Simsun,sans-serif;
  -webkit-touch-callout:none;
    -webkit-user-select:none;
    -khtml-user-select:none;
    -moz-user-select:none;
    -ms-user-select:none;
    user-select:none;
}
.green{
  color: green;
}
.red{
  color: red;
}
.grey{
  color: #666
}
.bbor{
  border-bottom: 1px solid #f1f1f1
}
.loaddata{
  width: 100%;
  text-align: center;
  background: #fff;
  height: 45px;
  line-height: 45px;
  color: #888;
  font-size: .22rem;
  border-top: 1px solid #f1f1f1
}

.tade_item{
  background: #fff;
}
.yd-progressbar>svg{
  position: absolute!important;
}
.trade_time{
  height: .8rem;
  line-height: .8rem;
  padding-left: .3rem;
  font-size: .28rem;
  background: #f4f4f4;
}
.trade_info{
  padding: .35rem .3rem .15rem .3rem;
}
.dian,.zwf{
  width: .12rem;
  height: .12rem;
  border-radius: .12rem;
  display: block;
} 
.dianblue{
  background: #278eed
}
.diancolorblue{
  color: #278eed
}
.diancoloryellow{
  color: #ed6d27
}
.dianyellow{
  background: #ed6d27
}
.trade_addr .left_d{
  float: left;
  font-size: .3rem;
  color: #333;
  margin-left: .15rem;
}
.trade_addr p{
  float: right;
  font-size: .32rem;
  text-align: right;
}
.trade_dabao{
  padding: .15rem .3rem .35rem .3rem
}
.succorfail span:nth-child(1){
  margin-left: .15rem;
  font-size: .25rem;
  color: #666;
  display: block;
  float: left;
}
.succorfail i{
  float: right
}
.succorfail span:nth-child(2){
  font-size: .25rem;
  color: #666;
  display: block;
  float: right;
}
.yd-confirm-hd{
  text-align: center!important;
}
</style>
<style>
.yd-pullrefresh-dragtip {
    pointer-events: none;
    position: absolute;
    top: 50px!important;
    left: 50%!important;
    -webkit-transform: translate(0);
    transform: translate(0);
    width: 36px!important;
    height: 36px;
    margin-left:-18px;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    color: #6e6e6e;
    font-size: 14px;
    text-align: center;
    background-color: rgba(240,250,250,.9);
    z-index: 9999!important;
    border-radius: 50%;
}
.yd-pullrefresh-dragtip-icon>i{
  margin:0!important
}
</style>



